.gallery {
    list-style-type: none;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.search-form__input {
    margin-left: 40%;
}

.gallery-item {
    margin-right: 20px;
    width: 30%;
}

.gallery :nth-child(3n) {
    margin-right: 0;
}

.gallery-item img {
    display: block;
    width: 100%;
}

.load-more-btn {
    margin-left: 40%;
}